@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
/* Common CSS */
* {
  font-family: "Montserrat", sans-se;
}

.head-underline {
  border-bottom: 2px solid rgba(75, 9, 9, 0.623);
  /* border-bottom-style: 3px solid rgb(75, 9, 9); */
  width: 85%;
  margin-top: 5px;
  margin-bottom: 1px;
}
.head-underline:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  -webkit-text-decoration: bold;
          text-decoration: bold;
}
.parent-head {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  cursor: pointer;
}
.navbar-logo {
  width: 50px;
  height: 50px;
  margin: 5px;
}
.nav-list-container {
  display: -webkit-flex;
  display: flex;
}

.no-border {
  border: none;
}

.underline {
  border-bottom: 2px solid rgb(75, 9, 9, 0.623);
  width: 50%;
}

.shadow-lg,
.shadow-md {
  border-radius: 10px;
}

.options-toggle {
  width: 45px;
  height: 45px;
  border-radius: 50%;
  border: none;
}

.bold-text {
  font-weight: bold;
}

.shadow-lg.roundedX {
  border-radius: 10px;
}

.text-black {
  color: black;
}

@media (min-width: 600px) {
  /* Desktop Mode */
  .nav-link {
    margin: 0px;
  }
  .nav-list-container {
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
  }
}

@media (max-width: 600px) {
  /* Mobile View */
  .nav-link {
    margin: 0 10px;
  }
  .container p.text-black {
    margin: 3px auto;
  }
}

/* Common CSS */

.mayank-img {
  border-radius: 50%;
  margin: auto;
}

.left-card {
  text-align: center;
  margin: 5px;
}

.social-links {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}
.social-links img {
  margin: 5px 10px;
  border-radius: 50%;
  width: 40px;
  height: 40px;
}

.social-links img:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
@media (min-width: 600px) {
  /* Desktop Mode */
  .mayank-img {
    width: 40%;
  }
}

@media (max-width: 600px) {
  /* Mobile View */
  .mayank-img {
    width: 85%;
  }
}

/* Common CSS */
.project-logo {
  border-radius: 50%;
  margin: 5px;
}

.project-logo:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
.project-navigation {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-flex-direction: row;
          flex-direction: row;
}

.readme img {
  max-width: 100%;
}
@media (min-width: 600px) {
  /* Desktop Mode */
  .project-logo {
    width: 75px;
    height: 75px;
  }
}

@media (max-width: 600px) {
  /* Mobile View */
  .project-logo {
    width: 45px;
    height: 45px;
  }
}

